<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content" v-if="chkImpServiceMapping">

            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ImportServices') }}</span>
                        <span class="p-actions float-right">
                            <RouterLink to="/Ticketing/ServiceListing" :title="$t('BacktoList')"
                                class="p-action-btn text-white float-right"><em
                                    class="fa fa-angle-double-left pr-2"></em><br />{{ $t('BacktoList') }}</RouterLink>
                        </span>
                    </div>
                </div>
            </div>

            <div class="border p-3" id="divMainLeadData">
                <div id="CrmImportMain">
                    <div class="row">
                        <div class="col-lg-12">
                            <div class="form-group">
                                <label>{{ $t('Download') }} {{ $t('SampleFile') }}:</label>
                                <div class="d-flex align-items-center">
                                    <p><a href="/Content/files/LeadImportSampleFile.xlsx" checkprivilege="no"
                                            class="text-dark" download>
                                            <img src="/Content/images/excel-icon.png" alt="" class="mr-2" />{{ $t('Service')
                                            }}
                                            <em class="fa fa-download ml-2 text-dark"></em></a></p>
                                </div>
                            </div>
                        </div>
                        <div class="col-xl-4 col-lg-4 col-md-6">
                            <div class="form-group">
                                <label>{{ $t('UploadFile') }}:<span class="text-danger">*</span></label>
                                <div class="custom-file">
                                    <input type="file" class="downloadexcel" id="flUserFile" size="19" name="flUserFile"
                                        tabindex="1" @change="setFile" />
                                </div>
                            </div>
                        </div>

                        <div class="col-12 mt-3">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:void(0);" id="uploadfile" :title="$t('ImportFromExcelFile')"
                                        @click="UploadServiceFile" class="btn btn-info my-2 mr-2"><em
                                            class="fa fa-file-excel-o mr-2" aria-hidden="true"></em>{{
                                                $t('ImportFromExcelFile') }}</a>
                                    <a href="javascript:void(0);" @click="resetFile" :title="$t('Reset')"
                                        class="btn btn-danger mr-2">
                                        <em class="fa fa-refresh mr-2 "></em>{{ $t('Reset') }}
                                    </a>

                                    <RouterLink to="/Ticketing/ServiceListing" :title="$t('BacktoList')"
                                        class="btn btn-secondary my-2">
                                        <em class="fa fa-angle-double-left pr-2"></em> {{ $t('BacktoList') }}
                                    </RouterLink>
                                </div>
                                <div class="col-lg-6">
                                    <div class="py-2"><span class="text-danger"><small class="text-danger">{{
                                        $t('MandatoryString') }}</small></span></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-lg-12 p-0 float-left">
                <div class="listing mt-2">
                    <div class="table-responsive">
                        <table id="tblbulkservice" class="table table-bordered dt-responsive nowrap m-0" style="width:100%;"
                            aria-describedby="importlead">
                            <thead class="thead-bg">
                                <tr>
                                    <th scope="col">Table Column</th>
                                    <th scope="col">Excel Column</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td colspan="2" class="text-center text-danger norecord no-record">{{
                                        $t('NoRecordfound') }}
                                    </td>
                                </tr>
                                <tr id="trdata" crtrwid="1001" class="d-none">
                                    <td>
                                        <select class="form-control d-none" name="TableColumns">
                                            <option value="">{{ $t('Select') }} {{ $t('Column') }}</option>
                                        </select>
                                        <select class="form-control" id="ddlTableColumn" name="TableColumn"></select>
                                    </td>
                                    <td>
                                        <select class="form-control" name="ExcelColumns">
                                            <option value="">{{ $t('Select') }} {{ $t('Column') }}</option>
                                        </select>
                                        <select class="form-control" id="ddlExcelColumn" name="ExcelColumn"></select>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>

                    <div class="col-lg-12 text-right mt-3 p-0">
                        <input type="hidden" id="hdnBatchId" value="Batch_id" />
                        <a v-if="showSaveButton" href="javascript:void(0);" @click="SaveServiceFile" id="savebtn"
                            :title="$t('Next')" class="btn btn-success formbtn widthhalf"><em
                                class="fa fa-save pr-2"></em><span>{{ $t('Next') }}</span></a>
                    </div>
                </div>
            </div>
        </section>
        <ImpWorkTypeMapping v-if="chkImpWorkTypeMapping" :ImpServiceTableMapping="ImpServiceTableMapping"
            :batchId="batchId">
        </ImpWorkTypeMapping>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import ImpWorkTypeMapping from '../../components/Modal/ImportWorkTypeMapping.vue';
export default {
    components: {
        ImpWorkTypeMapping
    },
    data() {
        return {
            isLoading: false,
            file: null,
            filepath: null,
            excelFileName: null,
            allrequiredcol: '',
            requiredcol: '',
            showSaveButton: false,
            ImpServiceTableMapping: '',
            batchId: '',
            chkImpWorkTypeMapping: false,
            chkImpServiceMapping: true
        }
    },
    created: function () {
        var vm = this;
        if (vm.chkImpWorkTypeMapping == false) {
            vm.chkImpServiceMapping = true;
        }
        else {
            vm.chkImpServiceMapping = false;
        }
    },
    methods: {
        BackToList: function () {
            this.$router.push({ name: 'ServiceListing' });
        },
        setFile: function (event) {
            this.file = event.target.files[0];
            this.excelFileName = this.file ? this.file.name : '';
        },

        UploadServiceFile: function () {
            // // //debugger;
            var vm = this;
            vm.isLoading = true;
            const formdata = new FormData();
            formdata.append("file", vm.file);
            if (vm.file != undefined && vm.file != null) {
                DataServices.ImportServicePost(formdata).then(function (response) {
                    var data = response.data;
                    if (data.Status == 0) {
                        vm.filepath = data.Msg;
                        setTimeout(function () { vm.loadTableHeader(); }, 1000);
                        setTimeout(function () { vm.loadExcelHeader(data.Msg); vm.isLoading = false; }, 2000);
                    } else if (data.Status == 1) {
                        vm.ShowAlert(vm.$t("IsNotValidFileUpload"), "warning", true, vm.$t("Alert"));
                    }
                    else {
                        vm.isLoading = false;

                        vm.ShowAlert(data.Msg, "warning", true, vm.$t("Alert"));
                    }
                });
            }
            else {
                vm.isLoading = false;
                vm.ShowAlert(vm.$t("Please upload valid excel file."), "warning", true, vm.$t("Alert"));
            }
        },
        loadExcelHeader: function (filepath) {
            var vm = this;
            var param = `filepath=${filepath}&excelFileName=${vm.excelFileName}`;
            DataServices.GetServiceExcelColumn(param)
                .then(function (response) {
                    var data = response.data;
                    if (data.length != undefined) {
                        var select = $("#ddlExcelColumn");
                        select.empty();
                        data = data.filter(item => item.column_Name.trim().toLowerCase() !== 'work type');
                        $.each(data, function (index, itemData) {
                            $('#hdnBatchId').val(itemData.batch_id);
                            $("#ddlExcelColumn").append("<option value='" + itemData.column_Name + "'Column_length='" + itemData.column_Length + "'Data_Type='" + itemData.data_Type + "' >" + itemData.column_Name + "</option>");
                        });
                        vm.createMultiRows(data.length);
                        // Attach event listener to each select element
                        $("table#tblbulkservice tr.visible td select[name^='ExcelColumns']").change(function () {
                            vm.CheckDuplicateExcelRecord($(this).val());
                        });
                        // Attach event listener to each select element
                        $("table#tblbulkservice tr.visible td select[name^='TableColumns']").change(function () {
                            vm.CheckDuplicateTableRecord($(this).val());
                        });
                    }
                })
        },
        resetFile: function () {
            var vm = this;
            vm.file = null;
            vm.excelFileName = null;
            document.getElementById('flUserFile').value = '';
            // $("table#tblbulkservice tbody").empty();
            $('#uploadfile').removeClass("disabled");
            $("table#tblbulkservice select").removeClass('border border-danger');
            $('#savebtn').removeClass("disabled");
        },
        loadTableHeader: function () {
            var vm = this;
            DataServices.GetServiceTableColumn()
                .then(function (response) {
                    var data = response.data;
                    if (data.length != undefined) {
                        var select = $("#ddlTableColumn");
                        select.empty();
                        $.each(data, function (index, itemData) {
                            $("#ddlTableColumn").append("<option value='" + itemData.column_Name + "'Column_length='" + itemData.column_Length + "'Data_Type='" + itemData.data_Type + "' >" + itemData.column_Name + "</option>");
                        });
                    }
                })
        },
        createMultiRows(totalRecords) {
            try {
                var vm = this;
                // // //debugger;
                $('#uploadfile').addClass("disabled");
                this.showSaveButton = true;
                var rownumber = 1002;
                vm.isLoading = true;
                var table = $("table#tblbulkservice tr#trdata");
                var Exceloptions = "";
                var Tableoptions = "";
                $(".norecord").addClass("d-none");
                for (var i = 0; i < totalRecords; i++) {
                    $(table).clone().attr({ 'crtrwid': rownumber }).removeClass("d-none").addClass("visible")
                        .find("input,a,select,div").each(function () {
                            $(this).attr({
                                'id': function (_, id) { return id + "" + rownumber; },
                                'name': function (_, name) { return name + "" + rownumber; },
                                'value': function (_, value) { return value; }
                            });
                            $(this).removeClass("d-none");
                        }).end().appendTo("table#tblbulkservice");
                    Exceloptions = $('#ddlExcelColumn').children('option').clone();
                    Tableoptions = $('#ddlTableColumn').children('option').clone();
                    $("table#tblbulkservice tr.visible td select[id='ddlTableColumn" + rownumber + "']").remove();
                    $("table#tblbulkservice tr.visible td select[name='TableColumns" + rownumber + "']").attr('id', 'ddlTableColumn' + rownumber);
                    $("table#tblbulkservice tr.visible td select[name='TableColumns" + rownumber + "']").append(Tableoptions);
                    $("table#tblbulkservice tr.visible td select[id='ddlExcelColumn" + rownumber + "']").remove();
                    $("table#tblbulkservice tr.visible td select[name='ExcelColumns" + rownumber + "']").attr('id', 'ddlExcelColumn' + rownumber);
                    $("table#tblbulkservice tr.visible td select[name='ExcelColumns" + rownumber + "']").append(Exceloptions);
                    if (Exceloptions.length > i) {
                        var tc_datatype = $("#ddlTableColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('data_type');
                        var ex_datatype = $("#ddlExcelColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('data_type');
                        var tc_Value = $("#ddlTableColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('value');
                        var ex_Value = $("#ddlExcelColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('value');
                        if (tc_datatype != undefined && ex_datatype != undefined) {
                            if (tc_Value.toLowerCase() == ex_Value.toLowerCase()) {
                                $("#ddlTableColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('selected', 'selected');
                                $("#ddlExcelColumn" + rownumber + " option[value='" + Exceloptions[i].value + "']").attr('selected', 'selected');
                            }
                        }
                    }
                    rownumber++;
                }
                $("table#tblbulkservice tr.visible[crtrwid ='" + 1001 + "']").removeClass("visible").addClass("d-none");
                vm.isLoading = false;
            } catch (ex) {

            }
        },

        CheckDuplicateTableRecord: function (ctrlid) {
            // // //debugger;
            var objarr = [];
            var flag = true;
            $("table#tblbulkservice tr#trdata").each(function () {
                var ddlval = $(this).find("td").eq(0).find("select option:selected").text();
                objarr.push(ddlval);
            }); //end of each
            var controlid;
            var dupid2;
            var dupid;
            var indexno = 1002;
            flag = false;
            var i; var j;
            for (i = 1; i < objarr.length; i++) {
                controlid = '#ddlTableColumn' + indexno;

                for (j = i + 1; j < objarr.length; j++) {

                    if (objarr[i] == objarr[j] && objarr[i] != "Select Column") {
                        flag = true;

                        dupid = controlid;
                        dupid2 = '#ddlTableColumn' + (1002 + (j - 1));
                        break;
                    }
                }
                if (flag) {
                    break;
                }
                $(controlid).removeClass('border border-danger');
                $(savebtn).removeClass("disabled");
                indexno++;
            }
            if (flag) {
                $(dupid).addClass('border border-danger');
                $(dupid2).addClass('border border-danger');
                $(savebtn).addClass("disabled");
            }
            flag = false;
            $("table#tblbulkservice tr#trdata").each(function () {
                if ($(this).find("td").eq(1).find("select").hasClass("border-danger")) {
                    $(savebtn).addClass("disabled");
                }
            });
        },

        CheckDuplicateExcelRecord(ctrlid) {
            // // //debugger;
            var objarr = [];
            var flag = true;
            $("table#tblbulkservice tr#trdata").each(function () {
                var ddlval = $(this).find("td").eq(1).find("select option:selected").text();
                objarr.push(ddlval);
            });
            var controlid;
            var dupid2;
            var dupid;
            var indexno = 1002;
            var flag = false;
            var i; var j;
            for (i = 1; i < objarr.length; i++) {
                controlid = '#ddlExcelColumn' + indexno;
                for (j = i + 1; j < objarr.length; j++) {
                    if (objarr[i] == objarr[j] && objarr[i] != "Select Column") {
                        flag = true;
                        dupid = controlid;
                        dupid2 = '#ddlExcelColumn' + (1002 + (j - 1));
                        break;
                    }
                }
                if (flag) {
                    break;
                }
                $(controlid).removeClass('border border-danger');
                $(savebtn).removeClass("disabled");
                indexno++;
            }
            if (flag) {
                $(dupid).addClass('border border-danger');
                $(dupid2).addClass('border border-danger');
                $(savebtn).addClass("disabled");
            }
            flag = false;
            $("table#tblbulkservice tr#trdata").each(function () {
                if ($(this).find("td").eq(0).find("select").hasClass("border-danger")) {
                    $(savebtn).addClass("disabled");
                }
            });
        },


        SaveServiceFile: function () {
            this.isLoading = true;
            var vm = this;
            var xml = "<serviceentries>\n";
            var saveFlag = false;
            var hasSelection = false;

            function checkMandatoryColumns() {
                var mandatoryCols = vm.allrequiredcol.split(",");
                mandatoryCols.forEach(index => {
                    $("table#tblbulkservice tr.visible").each(function () {
                        if (!$(this).hasClass('alert-success') && index == $(this).find('#ddlTableColumn' + $(this).attr("crtrwid")).val()) {
                            vm.requiredcol = vm.requiredcol.replace(new RegExp(index + ',?'), '');
                        }
                    });
                });
                return vm.requiredcol.length > 0;
            }

            if (checkMandatoryColumns()) {
                $(document).scrollTop(0);
                this.isLoading = false;
                return;
            }

            $("table#tblbulkservice tr.visible").each(function () {
                var row = $(this);
                var rowid = row.attr("crtrwid");
                var ddlTableColumn = row.find('#ddlTableColumn' + rowid);
                var ddlExcelColumn = row.find('#ddlExcelColumn' + rowid);

                if (ddlTableColumn.val() !== "") {
                    hasSelection = true;
                    if (!row.hasClass('alert-success')) {
                        if (ddlExcelColumn.val() === "") {
                            var valid = CheckRequiredR("ddlExcelColumn" + rowid, vm.$t("PleaseSelectAnyRecordsForFurther"), "invalid-feedback");
                            if (valid !== '') {
                                $(document).scrollTop(0);
                                vm.isLoading = false;
                                return false;
                            }
                        } else {
                            saveFlag = true;
                            var original_column_name = ddlTableColumn.val();
                            var data_type = ddlTableColumn.find(":selected").attr('data_type').toLowerCase();
                            const pattern = /^\s*([^()]+)/;
                            const match = ddlExcelColumn.val().trim().toLowerCase().match(pattern);
                            let result = match ? match[1].trim() : "";
                            result = result.replace(/[^a-zA-Z0-9\s]+/g, "").replace(/ /g, "_").replace(/_+$/g, '');
                            var formattedText = result;
                            var excel_column_name = formattedText;
                            var excel_data_type = ddlExcelColumn.find(":selected").attr('data_type').toLowerCase();

                            xml += `\n<Service>
                    \n\t<batch_id>${$('#hdnBatchId').val()}</batch_id>
                    \n\t<row_id>${rowid}</row_id>
                    \n\t<original_column_name>${original_column_name}</original_column_name>
                    \n\t<column_name>${original_column_name}</column_name>
                    \n\t<data_type>${data_type}</data_type>
                    \n\t<excel_column_name>${excel_column_name}</excel_column_name>
                    \n\t<excel_data_type>${excel_data_type}</excel_data_type>
                    \n</Service>\n`;
                        }
                    }
                }
            });
            $("table#tblbulkservice tr.visible").toggleClass("border border-danger", !hasSelection);

            xml += "\n</serviceentries>\n";
            if (saveFlag) {
                var postJSON = {
                    xmltrackentries: xml
                };
                vm.ImpServiceTableMapping = JSON.stringify(postJSON);
                vm.batchId = $('#hdnBatchId').val();
                vm.chkImpWorkTypeMapping = true;
                vm.chkImpServiceMapping = false;
            }
            this.isLoading = false;
        }
    }
}
</script>