<template>
    <div>
        <section id="main-content" class="main-content">

            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('ImportServices') }}</span>
                    </div>
                </div>
            </div>
            <div class="container-fluid mt-2">
                <div class="row">
                    <div class="col-lg-12 mb-3">
                        <div class="heading-border mt-2">
                            <h5 class="h5"><span>Work Type mapping</span></h5>
                        </div>
                    </div>
                </div>
                <div class="col-lg-12 p-0 float-left">
                    <div class="listing mt-2">
                        <div class="table-responsive">
                            <table id="tblbulkleadoption" class="table table-bordered dt-responsive nowrap m-0"
                                style="width:100%;" aria-describedby="importlead">
                                <thead class="thead-bg">
                                    <tr>
                                        <th scope="col">Excel Work Type</th>
                                        <th scope="col">Existing Work Type</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <!-- Dynamic rows -->
                                    <tr v-for="(workType, index) in WorkTypeData" :key="index">
                                        <!-- Input for Excel Work Type -->
                                        <td>
                                            <input type="text" class="form-control"
                                                :class="{ 'border-danger': workType.saveAttempted && !workTypeMatchesExisting(workType.value) }"
                                                disabled="disabled" :id="'txtExcelWorkType' + index"
                                                :name="'txtExcelWorkType' + index" v-model="workType.value">
                                            <!-- Show validation error only for Excel Work Type -->
                                            <span v-if="workType.saveAttempted && !workTypeMatchesExisting(workType.value)"
                                                class="text-danger">This Work Type is not mapped</span>
                                        </td>
                                        <!-- Conditional rendering for Existing Work Type -->
                                        <td>
                                            <template v-if="workTypeMatchesExisting(workType.value)">
                                                <!-- Show input field if match -->
                                                <input type="text" class="form-control" disabled="disabled"
                                                    :id="'txtExistingWorkType' + index"
                                                    :name="'txtExistingWorkType' + index" :value="workType.value">
                                                <span class="hidden">{{ getExistingWorkTypeName(workType.value) }}</span>
                                            </template>
                                            <template v-else>
                                                <button class="btn btn-primary custom-btn" style="width: 100%;"
                                                    @click="OpenWorkTypePopup(index)">
                                                    <i class="fa fa-plus"></i> Create New
                                                </button>
                                            </template>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>

                        <div class="col-12 mt-3">
                            <div class="row flex-row-reverse">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" class="btn btn-success" @click="SaveData" title="Save"><i
                                            class="fa fa-floppy-o pr-2"></i><span>{{ $t('Save') }}</span></a>&nbsp;
                                    <RouterLink to="/Ticketing/ServiceListing" title="Cancel" class="btn btn-danger">
                                        <i class="fa fa-times pr-2"></i><span>{{ $t('Cancel') }}</span>
                                    </RouterLink>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <AddWorkTypePopup v-if="AddWorkTypePopup"></AddWorkTypePopup>
        </section>
    </div>
</template>
  
<script>
import DataServices from "@/services/DataService";
import AddWorkTypePopup from '../../components/Modal/AddWorkTypePopup.vue'
export default {
    components: { AddWorkTypePopup },
    props: {
        ImpServiceTableMapping: {
            type: String
        },
        batchId: {
            type: String
        }
    },
    data() {
        return {
            file: null,
            filepath: null,
            AddWorkTypePopup: false,
            excelFileName: null,
            allrequiredcol: '',
            requiredcol: '',
            showSaveButton: false,
            WorkTypeData: [],
            ddlExistingWorkTypeOptions: []
        }
    },
    created() {
        this.FetchRecords();
    },
    methods: {
        FetchRecords() {
            //debugger;
            var url = `id=&moduleName=TICKETING&type=FSM_WORKTYPE&search=&refId=`;
            DataServices.GetDDLData(url).then(response => {
                if (response.status == 200) {
                    this.ddlExistingWorkTypeOptions = response.data.DATA;
                }
            }).then(() => {
                this.loadExcelWorkTypeHeaderValue();
            });
        },
        loadExcelWorkTypeHeaderValue() {
            var url = `batchId=${this.batchId}`;
            DataServices.GetWorkTypeTableValue(url).then(response => {
             //debugger;
                var data = response.data;
                if (data.length != undefined && data.length > 0) {
                    this.WorkTypeData = data.map(item => ({ value: item.value, saveAttempted: false }));
                    this.createMultiRows(data.length);
                }
            });
        },
        getExistingWorkTypeName(value) {
            const existingWorkType = this.ddlExistingWorkTypeOptions.find(option => option.name === value);
            return existingWorkType ? existingWorkType.value : '';
        },
        workTypeMatchesExisting(workTypeValue) {
            return this.ddlExistingWorkTypeOptions.some(item => item.name === workTypeValue);
        },

        createMultiRows(totalRecords) {
            try {
                var rownumber = 1001;
                $(".norecord").addClass("d-none");
                for (var i = 0; i < totalRecords; i++) {
                    rownumber++;
                }
            } catch (ex) {
                console.error("Error in createMultiRows: ", ex);
            }
        },
        OpenWorkTypePopup: function (id) {
            var vm = this;
           // //debugger;
            vm.AddWorkTypePopup = !vm.AddWorkTypePopup;
           // //debugger;
            if (vm.chkImpWorkTypeMapping == true) {
                vm.chkImpWorkTypeMapping = true;
            }
            else {

                vm.chkImpServiceMapping = false;
            }
        },
        SaveData() {
            var vm = this;
            const invalidWorkTypes = vm.WorkTypeData.filter(workType => !vm.workTypeMatchesExisting(workType.value));
            if (invalidWorkTypes.length > 0) {
                vm.WorkTypeData.forEach(workType => {
                    workType.saveAttempted = true;
                });
                return;
            }
            let xmlString = '<workTypes>';
            this.WorkTypeData.forEach((workType, index) => {
                xmlString += `<worktype><excelWorkType>${workType.value}</excelWorkType>`;
                const existingWorkTypeName = vm.getExistingWorkTypeName(workType.value);
                xmlString += `<existingWorkTypeValue>${existingWorkTypeName}</existingWorkTypeValue></worktype>`;
            });
            xmlString += '</workTypes>';
            var jsonobj = {
                xmlServiceTableMapping: vm.ImpServiceTableMapping,
                xmlWorkTypeMapping: xmlString
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveWorkTypeMapping(params).then(response => {
               // //debugger;
                if (response.data.status == 'Success') {
                    if (response.data.result == 1) {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Mapping')]), "Success", true, vm.$t('Alert'));
                        vm.$router.push({ path: '/Ticketing/ServiceListing' });
                    }
                    else{
                        vm.ShowAlert(vm.$t("ErrorWhileSaving", [vm.$t("Mapping")]), "danger", true, vm.$t("Alert"));
                    }
                   
                }
                vm.isLoading = false;
            })
        }
    }
}
</script>
  
<style>
.custom-btn {
    border-color: #25a5ff;
    color: #25a5ff;
    background-color: white;
}

.custom-btn:hover {
    background-color: #25a5ff;
    color: white;
}

.border-danger {
    border-color: red !important;
}
</style>
